@use 'base/variables' as v;

h1, h2, h3 {
    margin: 0;
    line-height: 1.2;

}
h1, h2 {
    font-weight: 400;
    font-family: 'Rubik Dirt', cursive;
}
h1 {
    font-size: 4.4rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 2.4rem;
    font-weight: 300;
}

p {
    font-size: 1.6rem;
    line-height: 1.8;
}