html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Outfit", sans-serif;
  font-size: 2rem;
  line-height: 1.5;
}

.contenedor {
  max-width: 120rem;
  margin: 0 auto;
  width: 95%;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
  font-family: "Rubik Dirt", cursive;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

.navbar {
  font-size: 2rem;
  padding: 2rem;
}

.navbar-collapse {
  align-items: center;
  justify-content: space-between;
}

.navbar-brand {
  display: none;
}
@media (min-width: 992px) {
  .navbar-brand {
    display: block;
  }
}

.hero {
  background-color: #f5f6f7;
  min-height: 45rem;
  text-align: center;
}
.hero__principal {
  padding: 3rem;
}
.hero__principal-img {
  width: 15rem;
  height: 15rem;
  margin: 2rem;
}
.hero__titulo {
  font-size: 2.5rem;
}
@media (min-width: 480px) {
  .hero__titulo {
    font-size: 4.4rem;
  }
}
.hero__texto {
  font-size: 2rem;
  color: #615151;
}
@media (min-width: 480px) {
  .hero__texto {
    font-size: 3.4rem;
  }
}
@media (min-width: 768px) {
  .hero__inferior-img {
    max-width: 60rem;
  }
}

.sobre-mi {
  height: 50rem;
  padding: 2rem;
  color: #FFFFFF;
  background-color: #1b1b32;
}
.sobre-mi__contenido {
  max-width: 60rem;
  text-align: center;
}
.sobre-mi__titulo {
  margin-bottom: 1rem;
}
.sobre-mi__descripcion {
  font-size: 2rem;
}

.experiencia {
  padding: 4rem 4rem 6rem 4rem;
}
.experiencia__columna {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  border: 0.2rem solid rgba(128, 128, 128, 0.3019607843);
  padding: 2rem;
  transition: all 0.3s ease-in;
}
.experiencia__columna:hover {
  color: #FFFFFF;
  background-color: #1b1b32;
}
.experiencia__columna:hover i {
  color: #1b1b32;
  background-color: #FFFFFF;
}
.experiencia__titulo {
  padding: 2rem;
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
}
.experiencia i {
  font-size: 4rem;
  color: #6F42C1;
  background-color: #1b1b32;
  padding: 1rem 2rem;
  border-radius: 50%;
}
.experiencia__badges {
  font-size: 2rem;
  font-weight: 900;
  margin: 1rem 0;
}

.badge {
  margin: 5px;
}

.proyectos {
  padding: 4rem;
}
.proyectos__contenido {
  padding-top: 6rem;
  margin-bottom: 4rem;
}
.proyectos__boton {
  font-size: 2rem;
  padding: 1.5rem;
  border-radius: 1rem;
  background-color: #1b1b32;
  color: #6F42C1;
  border: none;
}
.proyectos__boton:hover {
  background-color: #643ab0;
}

.proyecto {
  position: relative;
}
.proyecto:hover .proyecto__imagen {
  opacity: 0.2;
}
.proyecto:hover .proyecto__overlay {
  opacity: 1;
}
.proyecto__imagen {
  display: block;
  height: 100%;
  width: 100%;
  padding: 1rem;
  border-radius: 2rem;
  transition: all 0.3s ease;
}
.proyecto__overlay {
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  transition: all 0.3s ease;
}
.proyecto__titulo {
  font-weight: bold;
  font-size: 2rem;
}
@media (min-width: 400px) {
  .proyecto__titulo {
    font-size: 2.8rem;
  }
}
.proyecto__iconos {
  display: flex;
}
.proyecto i {
  color: #000;
  font-size: 4rem;
  margin: 1rem;
}
@media (min-width: 480px) {
  .proyecto i {
    font-size: 6rem;
  }
}

.articulos__titulo {
  margin-top: 5rem;
}
.articulos__boton {
  margin-top: 1rem;
  font-size: 2rem;
  padding: 1.5rem;
  border-radius: 1rem;
  background-color: #1b1b32;
  color: #6F42C1;
  border: none;
}
.articulos__boton:hover {
  background-color: #643ab0;
}
.articulos .card {
  width: 80%;
  max-width: 60rem;
  margin: 2rem;
}

.card-header {
  font-weight: bold;
}

.testimoniales {
  padding: 4rem 4rem 8rem 4rem;
}
.testimoniales .carousel {
  max-width: 80rem;
}
.testimoniales .carousel-item {
  height: 50rem;
}

.testimonial {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.testimonial__imagen {
  width: 12rem;
  height: 12rem;
  margin: 1rem 1rem 2rem 1rem;
}
@media (min-width: 768px) {
  .testimonial__imagen {
    width: 20rem;
    height: 20rem;
  }
}
.testimonial__descripcion {
  font-size: 1.2rem;
  max-width: 70%;
  text-align: center;
  font-family: "Share Tech Mono", monospace;
}
@media (min-width: 768px) {
  .testimonial__descripcion {
    font-size: 2rem;
  }
}
.testimonial__cliente {
  font-weight: bold;
  text-align: center;
  margin: 1rem;
}
.testimonial__nombre, .testimonial__cargo {
  line-height: 1.5;
  margin: 0;
}

.contacto {
  background-color: #1b1b32;
}
.contacto__contenedor {
  max-width: 110rem;
  min-height: 20rem;
  padding: 2rem;
}
.contacto__rectangulo {
  margin-top: -5rem;
  background-color: rgb(13, 110, 253);
  border-radius: 1rem;
  box-shadow: 0rem 0.1rem 0.4rem 0.1rem #FFFFFF;
}
.contacto .row {
  width: 100%;
  height: 15rem;
  display: flex;
  align-items: center;
}
.contacto__titulo {
  color: #FFFFFF;
}
.contacto__descripcion {
  color: #FFFFFF;
  font-size: 1.2rem;
}
.contacto__boton {
  color: #FFFFFF;
  font-weight: bold;
  background-color: transparent;
  border: 0.2rem solid #FFFFFF;
  padding: 1.5rem 2em;
  margin: 1rem;
  border-radius: 10rem;
  transition: all 0.3s ease-in-out;
}
.contacto__boton:hover {
  background-color: #FFFFFF;
  color: #000;
}

.footer {
  background-color: #1b1b32;
  color: #FFFFFF;
}
.footer__logo {
  width: 8rem;
  height: 8rem;
  margin: 1rem;
}
.footer__texto {
  font-size: 2rem;
  padding: 2rem;
  font-family: "Quicksand", sans-serif;
}
.footer__texto span {
  display: block;
}
.footer__icono {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 5rem;
  height: 5rem;
  padding: 1rem;
  margin: 1rem;
  border: 0.2rem solid #FFFFFF;
  border-radius: 50%;
  transition: all 0.2s ease-in;
}
.footer__icono:hover {
  background-color: #FFFFFF;
  border: 0.2rem solid rgb(13, 110, 253);
}
.footer__copyright {
  font-size: 1.5rem;
  color: #aeaeae;
  padding: 2rem;
}